<script setup lang="ts">
import { ref, watch, computed } from 'vue'
import { BaseModal, BaseButton } from '@/components/common'
import type { Member } from '@/types/entities'

interface Props {
  show: boolean
  member: Member | null
  isCreate: boolean
}

interface Emits {
  (e: 'close'): void
  (e: 'save', member: Member): void
  (e: 'update:show', value: boolean): void
}

const props = defineProps<Props>()
const emit = defineEmits<Emits>()

// è¡¨å•æ•°æ®
const form = ref<Partial<Member>>({
  id: '',
  name: '',
  role: '',
  avatar: '',
  bio: '',
  skills: [],
  grade: '',
  email: '',
  github: '',
  linkedin: '',
  joinDate: '',
  isActive: true
})

const newSkill = ref('')
const saving = ref(false)
const errors = ref<Record<string, string>>({})

// ç›‘å¬æˆå‘˜æ•°æ®å˜åŒ–ï¼Œåˆå§‹åŒ–è¡¨å•
watch(() => props.member, (newMember) => {
  if (newMember) {
    form.value = { ...newMember }
  } else {
    resetForm()
  }
}, { immediate: true })

// åŒå‘ç»‘å®š
const show = computed({
  get: () => props.show,
  set: (value: boolean) => emit('update:show', value)
})

// æ–¹æ³•
const resetForm = () => {
  form.value = {
    id: '',
    name: '',
    role: '',
    avatar: '',
    bio: '',
    skills: [],
    grade: '',
    email: '',
    github: '',
    linkedin: '',
    joinDate: '',
    isActive: true
  }
  newSkill.value = ''
  errors.value = {}
}

const addSkill = () => {
  if (!newSkill.value.trim()) return
  
  const skill = newSkill.value.trim()
  if (!form.value.skills!.includes(skill)) {
    form.value.skills!.push(skill)
    newSkill.value = ''
    delete errors.value.skills
  }
}

const removeSkill = (index: number) => {
  form.value.skills!.splice(index, 1)
}

const validateForm = (): boolean => {
  errors.value = {}
  
  if (!form.value.name?.trim()) {
    errors.value.name = 'è¯·è¾“å…¥æˆå‘˜å§“å'
  }
  
  if (!form.value.role?.trim()) {
    errors.value.role = 'è¯·è¾“å…¥èŒä½'
  }
  
  if (!form.value.email?.trim()) {
    errors.value.email = 'è¯·è¾“å…¥é‚®ç®±åœ°å€'
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.value.email)) {
    errors.value.email = 'è¯·è¾“å…¥æœ‰æ•ˆçš„é‚®ç®±åœ°å€'
  }
  
  if (!form.value.joinDate) {
    errors.value.joinDate = 'è¯·é€‰æ‹©åŠ å…¥æ—¶é—´'
  }
  
  if (!form.value.skills || form.value.skills.length === 0) {
    errors.value.skills = 'è¯·è‡³å°‘æ·»åŠ ä¸€ä¸ªæŠ€èƒ½'
  }
  
  if (form.value.avatar && !/^https?:\/\/.+/.test(form.value.avatar)) {
    errors.value.avatar = 'è¯·è¾“å…¥æœ‰æ•ˆçš„å›¾ç‰‡é“¾æ¥'
  }
  
  if (form.value.github && !/^https?:\/\/.+/.test(form.value.github)) {
    errors.value.github = 'è¯·è¾“å…¥æœ‰æ•ˆçš„GitHubé“¾æ¥'
  }
  
  if (form.value.linkedin && !/^https?:\/\/.+/.test(form.value.linkedin)) {
    errors.value.linkedin = 'è¯·è¾“å…¥æœ‰æ•ˆçš„LinkedIné“¾æ¥'
  }
  
  return Object.keys(errors.value).length === 0
}

const handleSubmit = async () => {
  if (!validateForm()) return
  
  saving.value = true
  
  try {
    // æ¨¡æ‹ŸAPIè°ƒç”¨
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    const memberData: Member = {
      id: form.value.id || `member_${Date.now()}`,
      name: form.value.name!.trim(),
      role: form.value.role!.trim(),
      avatar: form.value.avatar?.trim() || '',
      bio: form.value.bio?.trim() || '',
      skills: form.value.skills!,
      grade: form.value.grade || '',
      email: form.value.email!.trim(),
      github: form.value.github?.trim() || '',
      linkedin: form.value.linkedin?.trim() || '',
      joinDate: form.value.joinDate!,
      isActive: form.value.isActive!
    }
    
    emit('save', memberData)
  } catch (error) {
    console.error('ä¿å­˜æˆå‘˜ä¿¡æ¯å¤±è´¥:', error)
  } finally {
    saving.value = false
  }
}
</script>

